.App {
    text-align: center;
}

.Header {
    background: #61dafb;
    display: flex;


}

.Footer {
    background: #a289b7;
    display: flex;
    align-items: center;
}

.Content {
    font-size: 50px
}

.Logo {
    background: #74b68a;
    align-items: center;
    padding: 50px;
    display: inline-block;
}

.MenuItem {
    font-size: 40px;
    align-items: center;
    padding: 10px;
    display: inline-block;
    margin-right: 60px;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
